import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import MainFeaturedPost from './MainFeaturedPost'
import { API_ARTICLES,API_COMMENTS,BASE_URL } from "../constants/Endpoints"
import axios from 'axios'
import ReactPlayer from 'react-player'
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Header from './Header';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
const useStyles = makeStyles((theme) => ({
  body: {
    fontFamily: "Roboto,sans-serif",
    fontSize: "17px",
    backgroundColor: "#fdfdfd",
},

shadow: {
    boxShodow: "0 4px 2px -2px rgba(0, 0, 0, 0.1)",
},

masthead: {
    background: "#3398E1",
    height: "auto",
    paddingBottom: "15px",
    boxShadow: "0 16px 48px #E3E7EB",
    paddingTop: "10px"
},

card: {
    boxShadow: "0 16px 48px #E3E7EB"
},
container: {
  backgroundColor: "darkseagreen" 
}
   
  }));

  const sections = [
    { title: 'News', url: '/news/' },
    { title: 'Articles', url: '/articles/' },
  
  ];
export default function SingleFeaturedPost(props) {
    const classes = useStyles();
    const { postid } = props;
    const [post, setpost] = React.useState([]);
    const [comment, setComment] = React.useState([]);    
    let url = window.location.pathname
    let id = url.split('/').pop()
    let [values,setValues] = React.useState({
      "post":id,
      "name":null,
      "email":null,
      "body":null,
      "active": true
    })
    const handleInput = (e) =>{
      setValues({
          ...values,
          [e.target.name]:e.target.value
      })
    }
    const options = {
      headers: {
          'Content-Type': 'application/json',
      }
    };
    const handleSubmit =e=>{
      e.preventDefault()
      console.log(values,'kk')
      axios.post(API_COMMENTS,values,options).then(response=>{
        
        console.log(response,'success')
        getComments()
      })
      .catch(error=>{
        console.log(error,'error')
      })
      console.log(values)
      e.target.reset();
      }
    
      
    React.useEffect(()=>{
      getArticles()
      getComments()
       },[])
        const getArticles=()=>{
           axios.get(API_ARTICLES+id).then(response=>{
            console.log(response.data,'llllllll')
            setpost(response.data)}
        )}
        const getComments=()=>{
          axios.get(API_COMMENTS+id).then(response=>{
           console.log(response.data,'llllllll')
           setComment(response.data)}
       )}
        
    return (
      <React.Fragment>
      <CssBaseline />
      <Container maxWidth="lg">
        <Header title="Realize Stuff" sections={sections} />
        <main>
      <div className={classes.container}>
      <div class="row">
        <div class="col-md-8 card mb-4  mt-3 left  top">
          <div class="card-body">
    <h1>{post.name}</h1>
    <h4 class=" text-muted">{post.headline}</h4>
    <img style={{ height: '10%',width: '20%'}}src={BASE_URL+post.image} />
    <p class="card-text ">{post.content}</p>
    <ReactPlayer url={BASE_URL+post.videofile}
             width='70%'
             height='70%'
             style={{ marginLeft: "auto",
    marginRight: "auto",
    display: "block",
    }}
             controls = {true}
            />
          </div>
        </div>
        <h2> comments</h2>
        {comment.map(comment => {
          return(
        <div class="col-md-8 card mb-4  mt-3 ">        
      <div class="card-body">
        <div class="comments" style={{padding: "10px;"}}>
          <p class="font-weight-bold">
            {comment.name}
            <span class=" text-muted font-weight-normal">
              {comment.created_on}
            </span>
          </p>
          {comment.body}
        </div>
      </div>
    </div>
    ) })}
    <div>
<form onSubmit={handleSubmit} >
              <TextField
                name="name"
                label="name"
                onChange={handleInput}
id="name"
                // value={title}
                // onChange={this.handleChange}
                margin="normal"
              />&nbsp;
               <TextField
                name="email"
                label="email"
                onChange={handleInput}
id="email"
                // value={title}
                // onChange={this.handleChange}
                margin="normal"
              />&nbsp;
               <TextField
                name="body"
                label="comment"
                onChange={handleInput}
id="body"
                // value={title}
                // onChange={this.handleChange}
                margin="normal"
              /><br></br>
        <Button type="submit" color="primary">Submit</Button>
      </form>
  </div>

        </div>
        </div>
        </main>
        </Container>
        </React.Fragment>
    );
  }
    
  